.auth-page {
  background-image: url('~statics/images/auth-bg.png');
  background-size: contain;
  padding-top: 70px;
  padding-bottom: 70px;
  min-height: 720px;

  .auth-form {
    .auth-container{
      width: 370px;
      margin: auto;
      padding: 25px;
      background: #fff;
      border-radius: 5px;
    }
    
    .logo{
      width: 160px;
      height: 45px;
      margin: auto;
      margin-bottom: 1rem;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }

    .title {
      color: #003399;
      font-size: 24px;
      font-weight: bold;
    }

    .explain{
      color: #666666;
    }

    .register-footer{
      a{
        color: #0d6efd;
      }
    }

    .form {
      .ui-input-group{
        border-radius: 3px;
        border-top: none;
        border-right: 0;
        border-left: none;

        .group-prepend {
          padding: 0 4px !important;
          background: transparent !important;
          border: transparent !important;
        }
      }
    }

    .errors {
      width: 100%;
      text-align: center;
      color: #F6543E;

      .err:first-letter {
        text-transform: uppercase;
      }
    }
  }
}
