@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;1,200;1,300;1,700;1,800&family=Nunito:ital,wght@0,400;0,700;1,400;1,700&display=swap');

body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
}

img{
  max-width: 100%;
}

a {
  font-size: 14px;
  color: $colorDefault;
  text-decoration: none;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #F3F3F3; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #8a8989;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.font-weight-bold{
  font-weight: bold;
}

.ui-shadow{
  box-shadow: 0px 0px 4px rgb(0 0 0 / 25%);
}

.text-gray{
  color: #666666;
}

.radius-2{
  border-radius: 2px;
}

.text-color-buy{
  color: $colorBuy;
}

.bg-color-buy{
  background-color: $colorBuy;
}

.text-color-sell{
  color: $colorSell;
}

.bg-color-sell{
  background-color: $colorSell;
}

.App{
  padding-top: 90px;
}


header {
  position: fixed;
  top: 0;
  background-color: #fff;
  z-index: 88;
  height: 90px;
  .top-menu-button-home {
    width: 160px;

    img {
      max-width: 100%;
    }
  }

  .navbar-left {
    display: flex;
    flex-grow: 1;
    margin: 0 0 0 12px;
    opacity: 1;
    top: 0;
    transition: top 0.25s ease-in-out, opacity 0.25s ease-in-out;
    will-change: top, opacity;
    z-index: 0;
  }
}
