header {
    box-shadow: 0px 0px 4px rgb(0 0 0 / 25%);
}

.navbar-container{
    padding-top: 22px;
    padding-bottom: 22px;

    .top-menu-button-home {
        margin-right: 22px;
    }
    
    .navbar-left{
        li{
            margin-right: 19px;
        }
    }

    .navbar-right{
        .li-login, .li-register{
            margin-right: 25px;

            .ui-button{
                padding-left: 24px;
                padding-right: 24px;
            }
        }

        .li-app{
            margin-right: 11px;
            .ui-svg-icon{
                margin-right: 10px;
            }
        }

        .li-language{
            .ui-button{
                .ui-svg-icon:first-child, span{
                    margin-right: 7px;
                }
            }
        }
        
        .li-app, .li-language{
            .ui-button{
                background: rgba(0, 51, 153, 0.1);
                border: none;
                box-shadow: 0px 0px 4px rgb(0 0 0 / 25%);
                color: #333333;
            }
        }
    }

    .balance{
        color: #999999;
        cursor: pointer;

        i{
            margin-left: 3px;
        }

        &:hover{
            color: #333;
        }
    }
}

.ui-dropdown--lang {
    .ui-dropdown-list{
        width: 115px;

        .ui-dropdown-item{
            button{
                display: inline-flex;
                justify-content: end;
                padding-right: 3px;
                border: 0px !important;
                box-shadow: none !important;
                font-weight: normal !important;

                span{
                    display: flex;
                    align-items: center;
                    margin-right: 0 !important;
                    
                    .ui-svg-icon{
                        margin-left: 5px;
                        margin-right: 0 !important;
                    }
                }
            }

            &:hover{
                button{
                    background-color: #EFEFEF !important;
                }
            }
        }
    }
}

.ui-dropdown--store{
    .ui-dropdown-list{
        width: 115px;

        .ui-dropdown-item {
            height: 30px;
            // margin-bottom: 0 !important;

            .ui-button{
                box-shadow: none !important;
                padding: 0px;
    
                .ui-svg-icon {
                    margin-right: 0px !important;
                    width: 100% !important;
                }
            }
        }
    }
}

.ui-dropdown--user{
    .btn-user {
        background: transparent !important;
        border: 0 !important;
        display: flex;
        flex-direction: column;
    }

    .ui-button-group{
        box-shadow: none;
    }

    .ui-dropdown-list{
        width: 250px;

        .dropitem-header{
            color: #999999;
            text-align: right;

            span{
                color: #333;
            }
        }

        .offer-dropdown__item {
            padding: 0.7rem 0.5rem;

            &:hover{
                background-color: #EDEDED;
            }
        }
    }

    .separator{
        height: 1px;
        width: calc(100% - 1rem);
        background-color: #999999;
        margin: 5px auto;
    }
}

.ui-dropdown--noti{
    .ui-button{
        position: relative;

        span{
            position: absolute;
            top: -6px;
            right: -6px;
            border-radius: 1000px;
            border: 3px solid #fff;
            font-size: 7px;
            background: #FF6B6B;
            padding: 2px;
            line-height: 1em;
        }

        .noti-status{
            color: #999999;

            &:read{
                color: #076BFF;
            }
        }

        .datetime {
            border-bottom: 1px solid #ccc;
            padding-bottom: 10px;
            color: #999999;
        }
    }

    .ui-dropdown-list {
        max-height: 500px;
        overflow-y: auto;
    }

    .offer-dropdown__item {
        padding: 0.5rem 0.5rem;
        color: #333333;
    }

    .dropitem-header{
        padding: 0px 9px;
    }
}