@import 'statics/styles/_variables.scss';

.fiat-page{
    .fiat-sidebar-wrapper {
        background: #F1F2F6;
        padding: 0px 24px;

        & > div{
            width: 290px;
        }
        .filter-offer{
            .filter-offer-container{
                .pm{
                    overflow: hidden;
                    
                    &::after{
                        content: "";
                        display: inline-block;
                        height: 1px;
                        vertical-align: middle;
                        width: 100%;
                        margin-right: -100%;
                        margin-left: 8px;
                        border-top: 1px solid #d8d8d8;
                    }
                }
            }
        }
    }

    .fiat-main-wrapper{
        padding: 20px 0 20px 43px;
        
        .fiat-main-container{
            h1{
                color: $colorPrimary;
                font-size: 24px;
                font-weight: bold;
            }

            .fiat-listing-head{
                background: #F3FAFF;
                padding: 16px 16px;

                .listing-col{
                    display: flex;
                    align-items: center;
                    width: 23%;
                    padding: 0;

                    &:nth-child(2){
                        width: 28%;
                    }

                    &:nth-child(3){
                        width: 18%;
                    }
                    
                    &:last-child{
                        width: 31%;

                        & > div{
                            display: flex;
                            flex-grow: 1;
                            justify-content: space-between;
                            align-items: center;

                            .buttons{
                                flex-grow: 1;
                                text-align: right;
                            }
                        }
                    }
                }
            }

            .fiat-listing{
                position: relative;
                .fiat-loading{
                    text-align: center;
                }

                .listing-item{
                    padding: 16px 16px;
                    background: #fff;
                }

                .listing-col{
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    width: 23%;
                    padding: 0px;

                    & > div{
                        width: 100%;
                        text-align: left;
                    }

                    &:last-child{
                        width: 31%;
                    }

                    .name{
                        color: $colorPrimary;
                        font-weight: bold;
                        word-break: break-word;
                    }

                    .rating{
                        color: $colorTextGray;
                        & > span{
                            margin-right: 5px;
                        }
                    }

                    .merchant{
                        margin-left: -8px;
                        span {
                            &:first-child{
                                margin-right: 14px;
                            }
                        }
                    }

                    .user-online{
                        display: flex;
                        align-items: center;
                        i{
                            font-size: 10px;
                            margin-right: 10px;

                            &.online{
                                color: #0ECB81;
                            }
                        }

                        span{

                        }
                    }

                    &-1{
                        
                    }
                    &-2{
                        width: 28%;
                        .tranfer{
                            strong{
                                margin-right: 9px;
                            }
                        }

                        .banks {
                            padding-right: 10px;
                        }
                    }
                    &-3{
                        width: 18%;
                        & > div{
                            margin-top: 15px;
                            
                            .amount {
                                font-weight: bold;
                            }

                            .coinname{
                                margin-left: 5px;
                            }
                        }
                    }
                    &-4{
                        & > div{
                            text-align: right;

                            &.total{
                                font-weight: bold;
                                margin-bottom: 9px;
                            }

                            &.limit{
                                margin-bottom: 26px;
                                color: #666666;
                            }

                            .btn-fiat{
                                border-color: #0ECB81;
                                background-color: #0ECB81;
                                width: 80px;
                                height: 36px;
                                font-size: 18px;
                                font-weight: bold;
                            }
                        }
                    }
                }
            }

            .loadmore{
                .loadmore-right{
                    span{
                        margin-right: 16px;
                    }
                }
            }
        }
    }
}