.footer {
    border-top: 0.5px solid #666666;
    padding-top: 25px;
    padding-bottom: 25px;
    font-size: 16px;

    .footer-logo img {
        width: 200px;
    }

    .footer-social-link {
        margin-top: 33px;
        margin-bottom: 33px;

        & > a {
            margin-right: 16px;
        }

        .footer-copyright {
            font-size: 16px;
        }
    }

    ul{
        padding-left: 20px;
        li{
            margin-bottom: .85rem;

            &:first-child{
                font-weight: bold;
                margin-bottom: .9rem;
            }
        }
    }
}