.ui-modal-open{
    padding-right: 17px; 
    overflow: hidden;

    .ui-modal {
        overflow-x: hidden!important;
        overflow-y: auto!important;
    }
}

.ui-modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
    display: none;
    width: 100%;
    height: 100%;
    overflow: hidden;
    outline: 0;

    &.fade {
        transition: opacity .15s linear;
    }

    .ui-modal-dialog{
        position: relative;
        width: 740px;
        max-width: 100%;
        border-radius: 10px 10px 0 0;
        transform: none!important;
        z-index: 1051 !important;
        margin: 48px auto;
        .ui-modal-close {
            position: absolute;
            right: -32px;
            top: 0;
            font-size: 26px;
            line-height: 0;
            color: #ffffff;

            &:hover {
                color: red !important;
            }
        }

        .ui-modal-content {
            position: relative;
            display: flex;
            flex-direction: column;
            width: 100%;
            pointer-events: auto;
            background-color: #fff;
            background-clip: padding-box;
            border: 1px solid rgba(0, 0, 0, 0.2);
            border-radius: 0.3rem;
            outline: 0;
        }
    }

    .ui-modal-backdrop{
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1040;
        width: 100vw;
        height: 100vh;
        background-color: #000;
        
        &.show{
            opacity: .5;
        }
    }
}