.switch {
    width: 35px;
    height: 16px;
    display: inline-flex;
    /* padding: 12px !important; */
    z-index: 0;
    overflow: hidden;
    position: relative;
    box-sizing: border-box;
    flex-shrink: 0;
    vertical-align: baseline;
    cursor: pointer;

    .switch-track {
        width: 100%;
        height: 100%;
        opacity: 0.38;
        z-index: -1;
        transition: opacity 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        border-radius: 1000px;
        background-color: #2d2b2b;
    }

    .switch-underlay {
        top: 2px;
        left: 0;
        z-index: 1;
        position: absolute;
        flex: 0 0 auto;
        color: rgba(0, 0, 0, 0.54);
        padding-left: 2px;
        overflow: visible;
        font-size: 1.5rem;
        text-align: center;
        border-radius: 50%;
        transition: left 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, transform 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

        .switch-label {
            display: flex;
            align-items: inherit;
            justify-content: inherit;

            .switch-control {
                top: 0;
                left: 0;
                width: 100%;
                cursor: inherit;
                height: 100%;
                margin: 0;
                opacity: 0;
                padding: 0;
                position: absolute;
            }

            .switch-thumb {
                width: 12px;
                height: 12px;
                box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
                border-radius: 50%;
                background-color: #fff;
            }
        }

        &.on {
            transform: translateX(19px);

            .switch-thumb {
                background-color: rgb(0, 52, 198);
            }
        }

        &.off {}
    }

    &.switch-checked {
        .switch-track {
            opacity: .5;
            background: #eb5757;
        }
    }
}