@import 'statics/styles/_variables.scss';

.home-page {
  .section-search {
    padding: 45px 0 45px;
    background: #f3faff;
    margin-bottom: 70px;

    .search-form-container {
      box-shadow: 0px 0px 10px 5px #0000000d;
      background: #fff;
      padding: 30px 35px;
      border-radius: 5px;
    }

    .text-desc {
      padding-right: 33px;

      h1 {
        font-weight: bold;
        color: $colorPrimary;
        font-size: 48px;
      }
      h4 {
        color: #666666;
        font-size: 1.625rem;
      }
    }
  }

  .section-content{
    .section-order {
      margin-bottom: 80px;

      .section-item-header{
        .section-title{
          font-size: 36px;
          font-weight: 700;
        }
      }

      .section-item-container{
        padding: 10px 10px;
        .thumb{
          img{
            max-width: 100% ;
          }
        }

        h3{
          font-size: 18px;
          font-weight: bold;
        }
      }
    }
    
    .order-lg-1{
      .section-item-container{
        .thumb{
          width: 100px;
          margin: auto;

          img{
            max-width: 100% ;
          }
        }
      }
    }

    .order-lg-2{
      .section-item-container {
        justify-content: space-around;
        margin-bottom: 40px;

        .section-item{
          width: 30%;
          box-shadow: 0px 0px 4px rgb(0 0 0 / 25%);
          border-radius: 5px;
          padding: 18px;
          color: #555555;

          .thumb{
            margin-bottom: 15px;
            svg, path{
              fill: #8E8E8E;
            }
          }

          h3{
            
          }
        }
      }
    }

    .order-lg-3{
      .thumb{
        img{
          width: 600px;
        }
      }
    }

    .order-lg-4{
      .swiper {
        width: 100%;
        height: 100%;
      }
      
      .swiper-slide {
        text-align: center;
        font-size: 18px;
        background: #fff;
      
        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
      }
      
      .swiper-slide img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      .section-item-container{
        width: 848px;
        margin: auto;

        .section-item{
          border-radius: 5px;
          position: relative;
          padding: 25px 5px 5px 5px;

          .section-item-inner{
            padding: 10px 30px 30px 30px;

            .name {
              text-align: left;
              color: #0034C6;
              font-weight: bold;
              margin-bottom: 10px;
            }

            .desc {
                color: #666666;
                font-size: 0.875rem;
                text-align: left;
            }
          }

          .avatar {
            width: 50px;
            height: 50px;
            border-radius: 1000px;
            overflow: hidden;
            border: 5px solid #fff;
            margin-top: -34px;
            background: #ffff;
          }
        }
      }

      .next, .prev{
        position: absolute;
        top: calc(50% - 12px);
        font-size: 38px;
        z-index: 9;
        cursor: pointer;
      }

      .prev {
        left: -59px;
      }
      
      .next {
        right: -59px;
        text-align: right;
      }
    }
  }
}