@import '~statics/styles/_variables.scss';

@mixin button-type($bgColor, $borderColor, $textColor) {
	background: $bgColor;
	border: 1px solid $borderColor;
	color: $textColor;

	&:hover {
		background: darken($bgColor, 5%);
	}

	&.is-outline {
		background: #fff;
		border: 1px solid $borderColor;
		color: $bgColor;

		&:hover {
			background: #f0f0f0;
		}
	}
}

.ui-button {
	outline: none;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	white-space: nowrap; 
	cursor: pointer;
	text-align: center;
	transition: .1s;
	border: 1px solid transparent;
	text-overflow: ellipsis;
	border-radius: 5px;

	.button-icon {
		margin-right: 10px;
	}

	&.button-default {
		@include button-type($colorDefault, $colorDefault, #fff);

		&.is-outline {
			color: $colorDefault;
			border-color: transparent;
			box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
		}
	}

	&.button-primary {
		@include button-type($colorPrimary, $colorPrimary, #fff);
	}

	&.button-success {
		@include button-type($colorSuccess, $colorSuccess, #fff);
	}

	&.button-info {
		@include button-type($colorInfo, $colorInfo, #fff);
	}

	&.button-warning {
		@include button-type($colorWarning, $colorWarning, #fff);
	}

	&.button-danger {
		@include button-type($colorDanger, $colorDanger, #fff);
	}

	&.button-white {
		@include button-type(#fff, #ddd, #333);
	}

	&.button-link {
		@include button-type(#fff, #ddd, #333);
		border: none !important;
	}

	&.size-default {
		height: 38px;
		padding: 0 8px;
		font-size: 14px;
	}

	&.size-medium {
		height: 34px;
		padding: 0 20px;
		font-size: 14px;
	}

	&.size-small {
		height: 30px;
		padding: 0 15px;
		font-size: 12px;
	}

	&.size-mini {
		height: 26px;
		padding: 0 15px;
		font-size: 12px;
	}

	&.is-disabled {
		cursor: not-allowed !important;
		background-color: #f0f0f0 !important;
		border: 1px solid #f0f0f0 !important;
		color: #d0d0d0 !important;
	}

	&:active{
		outline:0;
		box-shadow:inset 0 4px 6px rgba(51,51,51,.15);
	}
}

.ui-button-square {
	padding: 0.1875rem !important;
}

.ui-button + .ui-button {
    margin-left: 10px;
}

.ui-button-group {
	border-radius: 5px;
	overflow: hidden;
	display: flex;
    align-items: center;
    justify-content: center;
	box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);

	.ui-button {
		margin: 0px;
		border-radius: 0;
		box-shadow: none !important;

		&:first-child {
			border-top-left-radius: 5px;
			border-bottom-left-radius: 5px;
		}

		&:last-child {
			border-top-right-radius: 5px;
			border-bottom-right-radius: 5px;
		}
	}

	&.ui-button-group--separator{
		.ui-button{
			border-left: none;
			border-right: 0.5px solid $colorBorderDefault;

			&:last-child{
				border-right: none !important;
			}
		}
	}
}