@import 'statics/styles/_variables.scss';

.ui-droplist {
	padding-top: 1px;

	.droplist-item {
		p {
			background-color: #fff;
			padding: 7px 10px;
			cursor: pointer;
			display: flex;
			align-items: center;

			&.selected {
				background-color: #f0f0f0;
			}

			&:hover {
				background-color: $colorPrimary;
				color: #fff;
			}

			&.is-bold {
				font-weight: bold
			}

			&.no-select {
				pointer-events: none;
			}

			&.disabled {
				pointer-events: none;
				color: #999;
			}
		}

		&.disabled {
			pointer-events: none;
			color: #999;
		}
	}

	.droplist-empty {
		padding: 10px;
	}

	.droplist-loading {
		padding: 10px;
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;

		i {
			margin-right: 5px;
		}
	}
}