.ui-dropdown{
    position: relative;
    
    .ui-dropdown-toggle{
        cursor: pointer;
    }

    .ui-dropdown-list{
        position: absolute;
        display: none;
        top: calc(100% + 2px);
        right: 0;

        &.open{
            display: block;
        }

        .ui-dropdown-item{
            margin-bottom: 8px;

            &:last-child{
                margin-bottom: 0px;
            }

            .ui-button{
                // margin-right: 0 !important;

                span{
                    margin: 0 !important;
                    .ui-svg-icon{
                        // margin-right: 0 !important;
                    }
                }
            }
        }
    }
}