@import 'statics/styles/_variables.scss';

.create-offer-page{
    .offer-main-wrapper{
        padding: 30px 30px 30px 0;

        h1 {
            font-weight: bold;
            font-size: 24px;
            line-height: 110%;
            margin-bottom: 30px;
        }

        .timeline{
            .timeline-item{
                margin-bottom: 8px;

                .itemline-title{
                    display: flex;
                    align-items: center;
                    font-weight: bold;
                    color: $colorPrimary;
                    margin-bottom: 4px;

                    & > div{
                        margin-right: 14px;
                    }
                }

                .timeline-content {
                    padding: 10px 0 10px 38px;
                    position: relative;

                    .label{
                        color: #003399;
                        font-size: 0.875rem;
                        margin-bottom: 7px;
                    }

                    &::after{
                        content: "";
                        top: 0;
                        left: 12px;
                        height: 100%;
                        position: absolute;
                        border-left: 1px solid #999999;
                    }
                }

                &-1{
                    
                }
                &-2{
                    .radio-button{
                        align-items: start;

                        &:first-child{
                            margin-bottom: 35px;
                        }
                        .radio-label{
                            display: block;
                            & > div{
                                font-size: 18px;
                                &:first-child{
                                    color: #333333;
                                    line-height: 1em;
                                    margin-bottom: 5px;
                                }
                            }
                        }
                    }
                }
                &-3{
                    .ppm-header{
                        background: #fff;
                        box-shadow: none;

                        .ppm-header-container{
                            padding-right: 0px;
                            padding-left: 0px;
                        }
                    }
                    .ppm-content{
                        padding-left: 0px;
                        padding-right: 0px;
                    }
                }
                &-4{
                    .timeline-content{
                        & > div{
                            margin-bottom: 20px;

                            &.price-type{
                                label.ui-radio{
                                    background: #FFFFFF;
                                    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
                                    border-radius: 5px;
                                    padding: 11px;
                                    width: 302px;

                                    &:first-child{
                                        margin-right: 43px;
                                    }

                                    .radio-label{
                                        display: block;
                                        font-size: 14px !important;
                                        div{
                                            
                                            &:first-child{
                                                margin-bottom: 5px;
                                            }
                                        }
                                    }
                                }
                            }

                            &.unit-price{
                                border-bottom: 1px solid #666666;
                                .fst-italic{
                                    font-size: 11px;
                                }
                            }
                        }
                    }
                }
                &-5{
                    
                }
            }
        }
    }

    .offer-sidebar-wrapper {
        background: #F1F2F6;
        padding: 30px 42px;

        .sidebar-container{
            width: 300px;

            .preview-ads {
                background: #FFFFFF;
                box-shadow: 0px 0px 5px rgb(0 0 0 / 10%);
                border-radius: 5px;
                border-bottom: 5px solid #0ECB81;
                margin-bottom: 17px;

                .preview-ads-heading {
                    padding: 9px 12px;
                    border-bottom: 1px solid #999999;

                    .buyer-seller, buyer-seller, .coin-info{
                        .small-text{
                            font-size: 10px;
                        }
                    }

                    .coin-info{
                        text-align: right;
                    }

                    .buyer-seller{
                        .owner-ads{
                            font-weight: bold;
                            color: $colorBuy;
                        }
                    }

                    .buyer-seller{
                        .owner-ads{
                            font-weight: bold;
                        }
                    }
                }

                .preview-ads-content{
                    padding: 9px 12px;

                    & > div{
                        display: flex;
                        justify-content: space-between;
                        padding: 10px 0px;
                        border-bottom: 1px solid #DEDEDE;
                    }
                }
            }

            .top-best-ads{
                border-radius: 5px;
                overflow: hidden;
                .best-ads-header{
                    background: #E7F7EE;
                    text-align: center;
                }

                ul {
                    background-color: #fff;
                    margin-bottom: 0px;
                    li {
                        display: flex;
                        align-items: center;
                        padding: 10px 28px;
                        color: #666666;

                        span{
                            width: 30px;
                            height: 30px;
                            background: #E7F7EE;
                            box-shadow: 0px 0px 4px rgb(0 0 0 / 25%);
                            border-radius: 1000px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            margin-right: 19px;
                        }
                    }
                }
            }
        }
    }
}