@import 'statics/styles/_variables.scss';

.ui-dropdown--coinlist{
    .ui-dropdown-list{
        width: 100%;

        .ui-dropdown-item{
            margin-bottom: 0 !important;
            & > button{
                border: 0;
                width: 100% !important;
                height: 46px !important;
                justify-content: start;
                display: flex;

                &:hover {
                    background-color: #f4fafd!important;
                }

                span{
                    display: flex;
                    align-items: center;
                    font-weight: normal;


                    // .ui-svg-icon{
                    //     margin-left: 5px;
                    // }
                }
            }
        }
    }
}

.ui-dropdown--currency{
  display: block;
  max-height: 500px;
  overflow: auto;
  .offer-dropdown__item{
    padding: 0.5rem 0;

    & > span{
      font-weight: normal !important;
      & > span{
        &:last-child{
          border: 1px solid #d8d8d8!important;
          border-radius: 4px!important;
          padding: 3px 0;
          min-width: 60px;
          text-align: center;
        }
      }
    }
  }
}

.ppm-modal {
    .ppm-header {
      background: #f3faff;
      box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
      border-radius: 5px;
  
      .ppm-header-container {
        padding: 18px 41px;
  
        .ppm-navbar {
          color: #9e9e9e;
          margin-bottom: 15px;
  
          .ppm-nav-item {
            margin-right: 18px;
            padding: 8px 13px;
            border-bottom: 1px solid #9e9e9e;
            transition: color 0.25s ease-in-out;
  
            &:hover,
            &.ppm-active {
              color: $colorPrimary;
  
              svg,
              path {
                fill: $colorPrimary !important;
              }
            }
  
            .ui-svg-icon {
              margin-right: 13px;
            }
          }
  
          .ppm-sellect-all {
            color: #076bff;
            align-self: flex-end;
          }
        }
  
        .ppm-search-form {
          .ui-input-group {
            border-radius: 5px !important;
          }
  
          .group-prepend {
            background: #fff !important;
            border-right: none !important;
            font-size: 15px;
            padding: 0 19px;
          }
        }
      }
    }
  
    .ppm-content {
      padding: 18px 41px;
      overflow-y: auto;
      overflow-x: hidden;
      max-height: calc(100vh - 224px);
  
      h3{
        font-size: 18px;
      }
  
      button{
        white-space: pre-wrap;
        font-weight: bold !important;
        color: #666666 !important;
      }
    }
  }