.ui-dropdown{
    position: relative;
    
    .ui-dropdown-toggle{
        cursor: pointer;
    }

    .ui-dropdown-list{
        position: absolute;
        display: none;
        background-color: #fff;
        border: 1px solid #d8d8d8;
        box-shadow: 0 16px 24px rgb(51 51 51 / 8%);
        border-radius: 4px;
        padding: 8px;
        width: 400px;
        display: none;
        z-index: 9;

        &.open{
            display: block;
        }

