.ui-radio {
	&.radio-group {
		&.radio-horizontal {
			display: flex;

			.radio-button {
				margin-left: 20px;
				margin-bottom: 0;

				&:first-child {
					margin-left: 0;
				}
			}
		}
	}

	&.radio-button {
		margin-bottom: 10px;
		display: flex;
		align-items: center;

		&:last-child {
			margin-bottom: 0;
		}

		& > input {
			display: none;
		}

		.radio-input {
			background-color: #fff;
			border: 1px solid #ddd;
			border-radius: 50%;
			cursor: pointer;
			position: relative;
		}

		.radio-label {
			font-size: 13px;
			color: #333;
			margin-left: 10px;
			flex: 1;
			display: flex;
			align-items: center;
		}

		input[type=radio]:checked + .radio-input::after {
			content: '';
			position: absolute;
			cursor: pointer;
			border-radius: 50%;
		}
	}
}