.ui-switch2 {
    padding: 4px 5px;
    font-size: 14px;
    color: #666666;
    display: inline-block;
    width: auto;
    position: relative;
    background: #F1F1F1;
    border-radius: 5px;
    font-weight: bold;

    span{
        padding: 6px 13px !important;
        cursor: pointer;
        border-radius: 5px;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        display: inline-block;

        &.ui-switch2-active {
            color: #FFFFFF;
            background: #0ECB81;
        }
    }
}